	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-03-30-cache-valley/index.md absPath of file >>> MarkdownRemark","html":"<p>The Desert News is saying what we already know. <a href=\"http://deseretnews.com/article/1,5143,695265839,00.html\">Cache Valley is a great place to live</a>. Of course the numbers don't tell the actual story. This is a great place to live because it has a small town feel, the biking is awesome and people are friendly. The schools are great and you feel safe. The biggest drawback to living in Cache Valley is the low wages. For many jobs the university tends to pay very poorly and economic development around here so far has meant adding a Home Depot and second Walmart. Hopefully we start to see the kind of economic development that is sustainable and that provides good, well paying jobs.</p>","frontmatter":{"title":"Cache Valley","date":"March 30, 2008","image":null}}},"pathContext":{"path":"/cache-valley"}}

